const myheader = resolve => require(['../components/header_1.vue'], resolve);
const Info = resolve => require(['../components/improve_information.vue'], resolve);

export default{
  name:'register',
  data () {
    return {
      detailTitle:'游客注册',
      infoTitle:'完善信息',
      infoShow:false,
      confimPas:''
    }
  },
  methods:{
    next(){
      this.infoShow=true;
    },
    back(){
      this.infoShow=false;
    }
  },
  computed:{
    showButton(){
      if(this.confimPas){
        return true;
      }else{
        return false;
      }
    }
  },
  components:{
    myheader,
    Info,
  }
}



